html {
  --main-color: #00acdc;
  --header-height: 60px;
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.logo__pic {
  width: 40px;
  display: block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link {
  height: var(--header-height);
  color: #000;
  line-height: var(--header-height);
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.nav__item {
  margin-left: 20px;
}

.nav__item:first-of-type {
  margin-left: 0;
}

.nav__link:hover {
  color: var(--main-color);
}

.is-active {
  color: var(--main-color);
  position: relative;
}

.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: flex;
  position: absolute;
  top: 56px;
}
